import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import Page3 from "./component/page3";
import Page2 from "./component/page2";
import Page1 from "./component/page1";
import Electricity from "./component/electricity";
import Diesel from "./component/Diesel";
import Electricity2 from "./component/page3/electricity";
import Diesel2 from "./component/page3/diesel";


// This site has 3 pages, all of which are rendered
// dynamically in the browser (not server rendered).
//
// Although the page does not ever refresh, notice how
// React Router keeps the URL up to date as you navigate
// through the site. This preserves the browser history,
// making sure things like the back button and bookmarks
// work properly.

export default function BasicExample() {
    const style = {
        backgroundColor: "#4CAF50",
        border: "none",
        color: "white",
        padding: "15px 32px",
        textAlign: "center",
        display: "inline-block",
        fontSize: "20px",
        margin: "4px 2px",
        cursor: "pointer"
    }
    return (
        <Router>
            <div>
                <ul>
                    <Link to="/">
                        <button style={style}>Page1</button>
                    </Link>

                    <Link to="/page2/gasoline">
                        <button style={style}>Page2</button>
                    </Link>
                    <Link to="/page3/gasoline">
                        <button style={style}>Page3</button>
                    </Link>
                </ul>

                <hr/>


                <Switch>
                    <Route exact path="/">
                        <Page1/>
                    </Route>
                    <Route path="/page2/gasoline">
                        <Page2/>
                    </Route>
                    <Route path="/page3/gasoline">
                        <Page3/>
                    </Route>
                    <Route exact path="/page2/electricity">
                        <Electricity/>
                    </Route>
                    <Route exact path="/page2/diesel">
                        <Diesel/>
                    </Route>
                    <Route exact path="/page3/electricity">
                        <Electricity2/>
                    </Route>
                    <Route exact path="/page3/diesel">
                        <Diesel2/>
                    </Route>
                </Switch>
            </div>
        </Router>
    );
}




