import * as d3 from 'd3';
import "./drawchart.css"


export default function secondChart(height, width, data, flag) {

    let makerlistGasoline = ["Acura", "Alfa Romeo", "Aston Martin", "Audi", "Bentley", "BMW", "Buick", "Cadillac",
        "Chevrolet", "Chrysler", "Dodge", "Ferrari", "Fiat", "Ford", "Genesis", "GMC", "Honda", "Hyundai", "Infiniti",
        "Jaguar", "Jeep", "Kia", "Lamborghini", "Land Rover", "Lexus", "Lincoln", "Lotus", "Maserati", "Mazda",
        "McLaren Automotive", "Mercedes-Benz", "MINI", "Mitsubishi", "Nissan", "Porsche", "Ram", "Rolls-Royce",
        "Roush Performance", "smart", "Subaru", "Toyota", "Volkswagen", "Volvo"];
    let makerlistElectricity = ["BMW", "Chevrolet", "Fiat", "Ford", "Hyundai", "Kia", "Mercedes-Benz", "Mitsubishi", "Nissan", "Tesla"];
    let makerlistDiesel = ["BMW", "Chevrolet", "GMC", "Jaguar", "Land Rover"];

    let mesg = "Smart";
    let yGasoline = d3.scaleLinear()
        .domain([0, 39])
        .range([height, 0]);

    let yElectricity = d3.scaleLinear()
        .domain([0, 150])
        .range([height, 0]);

    let yDiesel = d3.scaleLinear()
        .domain([0, 39])
        .range([height, 0]);
    let div = d3.select("body").append("div")
        .attr("class", "tooltip")
        .style("opacity", 0);
    let set = {
        makerlist: makerlistGasoline,
        color: "red",
        y: yGasoline,
    };

    if (flag == 1) {
           mesg = "Smart !"
        set.makerlist = makerlistGasoline;
        set.color = "red";
        set.y = yGasoline;
    } else if (flag == 2) {
        mesg = "Hyundai !"
        set.makerlist = makerlistElectricity;
        set.color = "orange";
        set.y = yElectricity;
    } else if (flag == 3) {
        mesg = "Jaguar !"
        set.makerlist = makerlistDiesel;
        set.color = "blue";
        set.y = yDiesel;
    }


    let margin = 80;
    let x = d3.scaleBand()
        .domain(set.makerlist)
        .range([0, width])
        .padding(0.2);


    const svg = d3.select("#chart")
        .append("svg")
        .attr("width", width + 2 * margin)
        .attr("height", height + 2 * margin)
        // .style("border", "1px solid black");

    svg.append("g")
        .attr("transform", "translate(" + margin + "," + margin + ")")
        .selectAll("rect")
        .data(data)
        .enter().append("rect")
        .attr("width", x.bandwidth())
        .attr("height", function (d) {
            return height - set.y(d.val);
        })
        .attr("x", function (d, i) {
            return x(d.Maker);
        })
        .attr("y", function (d) {
            return set.y(d.val);
        })
        .style("fill-opacity", 0.5)
        .style("fill", set.color)
        .on("mouseover", function (d) {
            div.transition()
                .duration(200)
                .style("opacity", .9);
            div.html("Maker: " + d.Maker + "<br>" + "AvgCityMPG: " + d.val)
                .style("left", (d3.event.pageX) + "px")
                .style("top", (d3.event.pageY - 28) + "px");
        })
        .on("mouseout", function (d) {
            div.transition()
                .duration(500)
                .style("opacity", 0);
        });

    svg.append("g")
        .attr("transform", "translate(" + margin + "," + margin + ")")
        .call(d3.axisLeft(set.y));

    svg.append("g")
        .attr("transform", "translate(" + margin + "," + (height + margin) + ")")
        .call(d3.axisBottom(x))
        .selectAll("text")
        .attr("y", 0)
        .attr("x", 9)
        .attr("dy", ".95em")
        .attr("transform", "rotate(90)")
        .style("text-anchor", "start");

    svg.append("g")
        .attr("transform","translate(700,50)" )
        .append("text")
        .text(mesg)
        .attr("font-size","40px")


}